.sounds {
  background-color: #1e1e23;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: min(540px, 100%);
}

.sounds__item {
  align-items: center;
  display: flex;
  gap: 1em;
  justify-content: space-between;
  padding: 1em;
  border-bottom: 0.25em solid #000;
}
.sounds__item:last-of-type {
  border-bottom: none;
}
@media (max-width: 480px) {
  .sounds__item {
    flex-direction: column;
    text-align: center;
  }
}

.sounds__name p {
  margin: 0;
}

.sounds__btn {
  display: flex;
  gap: 1.5em;
}
.sounds__btn a,
.sounds__btn button {
  display: inline-block;
  width: 44px;
  height: 44px;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center center;
  transition: all 0.25s;
}
.sounds__btn a span,
.sounds__btn button span {
  display: none;
}
